<template>
  <div class="volume">
    <TimeSearch :showSearch="false" @timeChange="timeChange"></TimeSearch>
    <WholeData :nums="nums" title="团队数据"></WholeData>
    <div class="rank">
      <div class="rank-head">
        <MenuBar :list="menus" @change="menuChange"></MenuBar>
        <div class="rank-title flex ac js">
          <div>{{params.type === 'sale_num' ? '团队销售数量排行' : '团队销售额排行'}}</div>
          <div class="flex ac" @click="toggleSort">
            {{ params.order === 0 ? `按${params.type === 'sale_num' ? '销量' : '销售额'}由多到少排序` : `按${params.type === 'sale_num' ? '销量' : '销售额'}由少到多排序`}}
            <img
              :class="{top: params.order === 1}"
              :src="require('@/assets/bottom.png')"
              alt
            />
          </div>
        </div>
      </div>
      <div class="rank-list">
        <InfiniteList
          ref="list"
          v-slot="slotProps"
          api="teamManage/teamSaleAnalyzeDetail"
          :params="listParams"
          :formatData="formatData"
        >
          <div
            class="list-item flex column"
            v-for="(item, index) in slotProps.list"
            :key="item.user_id"
            @click="toDetail(item.user_id, item.head_pic, item.nickname)"
          >
            <div class="detail item flex ac js">
              <CustomImg width="1.6rem" height="1.6rem" fit="cover" :src="item.head_pic" />
              <div class="item goods-name">{{item.nickname | longtext}}</div>
              <div v-if="params.type === 'sale_num'" class="goods-num">
                {{item.sale_num}}
                <span>件</span>
              </div>
              <div v-if="params.type === 'sale_money'" class="goods-num">
                {{item.sale_money}}
                <span>元</span>
              </div>
              <img v-if="index === 0" class="rank-icon" :src="require('@/assets/rank-one.png')" alt />
              <img v-if="index === 1" class="rank-icon" :src="require('@/assets/rank-two.png')" alt />
              <img
                v-if="index === 2"
                class="rank-icon"
                :src="require('@/assets/rank-three.png')"
                alt
              />
            </div>
            <div class="list-item-btn text-center">查看商品销售情况</div>
          </div>
        </InfiniteList>
      </div>
    </div>
  </div>
</template>

<script>
import TimeSearch from "@/components/TimeSearch";
import WholeData from "@/components/WholeData";
import MenuBar from "@/components/MenuBar";
import InfiniteList from "@/components/InfiniteList";

export default {
  components: { TimeSearch, WholeData, MenuBar, InfiniteList },
  data() {
    return {
      params: {
        type: "sale_num", // 排行榜类型
        order: 0 // 排序
      }, // 接口参数,
      nums: [], // 整体数据
      menus: [
        { name: "团队销售数量排行", value: "sale_num" },
        { name: "团队销售额排行", value: "sale_money" }
      ] // 排行榜tab
    };
  },
  computed: {
    listParams() {
      const params = { ...this.params };
      delete params.timeStr;
      return params;
    }
  },
  methods: {
    // 时间段变化
    timeChange(params) {
      this.params = {
        ...this.params,
        ...params
      };
      this.$refs.list.resetData();
    },
    // 排序
    toggleSort() {
      this.params = {
        ...this.params,
        order: this.params.order === 1 ? 0 : 1
      };
      this.$refs.list.resetData();
    },
    // 排行榜菜单切换
    menuChange(index, value) {
      this.params = {
        ...this.params,
        type: value
      };
      this.$refs.list.resetData();
    },
    // 数据格式化
    formatData(res) {
      const { sale_num, sale_money, list } = res;
      const { data, current_page, last_page } = list;
      const nums = [];
      nums.push(
        { name: "总销量(件)", value: sale_num },
        { name: "总销售额(元)", value: sale_money }
      );
      this.nums = nums;
      return {
        data,
        current_page,
        last_page
      };
    },
    // 商品销售情况
    toDetail(user_id, head_pic, nickname) {
      const { time_type, start_time, end_time, timeStr, type } = this.params;
      const params = {
        time_type,
        start_time,
        end_time,
        timeStr,
        user_id,
        head_pic,
        nickname: nickname.replace(/%/g, '%25'),
        type
      };
      let str = "";
      for (let i in params) {
        str += `&${i}=${params[i]}`;
      }
      str = str.slice(1);
      this.$router.push(`/team/detail?${str}`);
    }
  }
};
</script>

<style lang="less" scoped>
.search {
  width: 18px;
  margin-right: 13px;
}

.time-desc {
  width: 349px;
  height: 43px;
  line-height: 43px;
  padding-left: 10px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 4px 0px rgba(193, 193, 193, 0.5);
  margin: 3px auto;
  color: #777;
  font-size: 14px;
}

.rank {
  background: #fff;
}

.rank-head {
  overflow: hidden;
  background: #fff;
  padding: 10px 13px 10px;
}
.rank-title {
  box-sizing: border-box;
  width: 349px;
  height: 24px;
  background: rgba(254, 250, 243, 1);
  color: #282828;
  font-size: 13px;
  line-height: 24px;
  margin: 18px auto 0;
  padding: 0 10px;
  img {
    width: 11px;
    margin-left: 6px;
  }
}

.list-item {
  box-sizing: border-box;
  width: 349px;
  height: 114px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 4px 0px rgba(193, 193, 193, 0.5);
  border-radius: 8px;
  margin: 10px auto;
}

.list-item-btn {
  height: 34px;
  line-height: 34px;
  color: #f6d9b4;
  font-size: 12px;
  background: rgba(254, 250, 243, 1);
  border-radius: 0 0 8px 8px;
}

.detail {
  padding: 0 10px;
  position: relative;
}

.rank-icon {
  width: 25px;
  position: absolute;
  top: 0;
  right: 13px;
}

.goods-name {
  color: #252525;
  font-size: 14px;
  line-height: 20px;
  padding: 0 8px;
}

.goods-num {
  color: #252525;
  font-size: 18px;
  font-weight: 500;
  span {
    font-size: 14px;
  }
}

.top {
  transform: rotate(180deg);
}
</style>